@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
.header{width:100%;display:flex;flex-direction:row;justify-content:flex-end;align-items:center}.header__btn{display:flex;align-items:center;font-size:20px;font-weight:bold;background-color:transparent;text-transform:uppercase;padding:10px;cursor:pointer}

.notification{position:absolute;bottom:20px;right:20px;z-index:99;min-width:20%;border:1px solid #ffeeba !important;border-radius:5px;color:#856404;background-color:#fff3cd;padding:20px;font-size:20px;font-weight:bold;transform-origin:right;animation:notify 3s ease-in-out infinite}@keyframes notify{0%{transform:scaleX(0);opacity:0}10%{transform:scaleX(1);opacity:1}16%{transform:scale(1)}55%{transform:scaleX(1);opacity:1}65%{transform:scaleX(0.25);opacity:0}95%{transform:scaleX(0.25)}100%{transform:scaleX(0)}}

.nav{font-size:16px;padding:36px 32px;background:var(--card-bg);-webkit-backdrop-filter:blur(20px);backdrop-filter:blur(20px);border-radius:16px}.nav__logo{width:100%;padding-bottom:36px}.nav__link{display:flex;align-items:center;width:100%;padding:12px 27px;text-decoration:none}.nav__link--active{background:var(--nav-active-bg);box-shadow:0px 0px 1px rgba(40,41,61,0.04),0px 2px 4px rgba(96,97,112,0.16);border-radius:8px;color:var(--nav-active-text)}.nav__link svg{margin-right:22px}

.container{display:flex;width:100vw;height:100vh;background:var(--bg);padding:32px}.container__right{width:100%}.container__right .main{position:relative;padding:52px}.bg-green-circle-1{z-index:0;position:absolute;width:472px;height:472px;left:-8px;top:560px;border-radius:100%;background:#91f466;opacity:0.5}.bg-green-circle-2{z-index:0;position:absolute;width:720px;height:720px;top:312px;right:-472px;border-radius:100%;background:#91f466;opacity:0.9}

.app{display:block;width:280px;height:456px;border-radius:16px;background:var(--card-bg);-webkit-backdrop-filter:blur(20px);backdrop-filter:blur(20px);padding:16px}.app div{margin-bottom:16px}.app__header{display:flex}.app__view-in-store{display:flex;align-items:center;justify-content:space-between}.app__view-in-store div{margin-bottom:0}.app__icon{width:88px;margin-right:16px;-webkit-filter:drop-shadow(0px 2px 4px rgba(40,41,61,0.04)),drop-shadow(0px 8px 16px rgba(96,97,112,0.16));filter:drop-shadow(0px 2px 4px rgba(40,41,61,0.04)),drop-shadow(0px 8px 16px rgba(96,97,112,0.16));border-radius:8px}.app__screenshots{overflow:hidden;display:flex}.app__screenshot{margin-right:8px}

.apps{height:calc(100vh - 64px - 52px - 44px);width:100%;overflow-x:hidden;overflow-y:auto;padding:24px}.apps__grid{width:100%;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:24px}@media (max-width: 1600px){.apps__grid{grid-template-columns:1fr 1fr 1fr}}@media (max-width: 1300px){.apps__grid{grid-template-columns:1fr 1fr}}@media (max-width: 1100px){.apps__grid{grid-template-columns:1fr}}


*{margin:0;padding:0;border:none;outline:none;box-sizing:border-box}body{--bg: #ebebea;--card-bg: rgba(245, 245, 244, 0.7);--text: #333333;--nav-active-bg: #333333;--nav-active-text: #f5f5f5}body.theme-dark{--bg: #1d1d1d;--card-bg: rgba(51, 51, 51, 0.65);--text: #ccc;--nav-active-bg: #55ef14;--nav-active-text: #525252}*{font-family:"Inter", sans-serif !important;color:var(--text)}body{overflow:hidden;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

